body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App .App-profile {
  text-align: center;
  background-color: #282c34; }
  .App .App-profile img {
    max-width: 300px; }

.App .App-content {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px; }

.App-header h1{
  font-family: 'Lobster', cursive;
  font-size: 30px;
}
.App-header .Section-width .App-header-name {
  padding-right: 10px;
}
.App-header .Section-width hr {
  -webkit-margin-start: 10px;
          margin-inline-start: 10px;
  -webkit-margin-end: 10px;
          margin-inline-end: 10px;
  color: #434343;
}
.App-header .Section-width .App-header-links {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  line-height: 87px;
}
.App-header .Section-width .App-header-links a {
  padding-right: 10px;
  padding-left: 10px;
  text-decoration: none;
  color: black;
}
.App-header .Section-width .App-header-links a:visited {
  text-decoration: none;
}
@media (min-width: 920px) {
  .App-header .Section-width {
    display: -webkit-flex;
    display: flex;
  }
}
.Section-width {
  max-width: 900px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}
@media (min-width: 920px) {
  .Section-width {
    padding-left: 0px;
    padding-right: 0px;
  }
}
