.App-header h1{
  font-family: 'Lobster', cursive;
  font-size: 30px;
}
.App-header .Section-width .App-header-name {
  padding-right: 10px;
}
.App-header .Section-width hr {
  margin-inline-start: 10px;
  margin-inline-end: 10px;
  color: #434343;
}
.App-header .Section-width .App-header-links {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  line-height: 87px;
}
.App-header .Section-width .App-header-links a {
  padding-right: 10px;
  padding-left: 10px;
  text-decoration: none;
  color: black;
}
.App-header .Section-width .App-header-links a:visited {
  text-decoration: none;
}
@media (min-width: 920px) {
  .App-header .Section-width {
    display: flex;
  }
}